* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-section {
  background-color: #417ef0;
  padding: 15px;
}

#search-form {
  font: inherit;
  max-width: 500px;
  min-height: 50px;
  margin: 0 auto;
  padding: 8px;
  display: flex;
}

input[type="text"] {
  border: none;
  border-radius: 5px;
  flex: auto;
  margin-right: 15px;
  padding: 0 15px;
}

button[type="submit"] {
  width: 80px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-left: -30px;
  display: flex;
}

.photo-card {
  height: 100%;
  flex-direction: column;
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
  display: flex;
}

.photo-card img {
  max-width: 100%;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more {
  width: 150px;
  color: #fff;
  background-color: #417ef0;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin: 40px auto 0;
  padding: 10px 15px;
  transition: background-color .5s, color .5s;
  display: block;
}

.load-more:hover {
  color: #417ef0;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #417ef0;
}

